import { FunctionComponent, useCallback, useRef, useState } from "react";
import "./DigitalArtPage.css";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { faHome } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const works = [
  {
    title: "Portrait",
    imageUrl: "/MyWebsite/Arts/Subha.png",
    imageMockUp: "/MyWebsite/Arts/SubhaMockUp.png",
  },
  {
    title: "Portrait",
    imageUrl: "/MyWebsite/Arts/Donya.png",
    imageMockUp: "/MyWebsite/Arts/DonyaMockUp.png",
  },
  {
    title: "Portrait",
    imageUrl: "/MyWebsite/Arts/Dhara.png",
    imageMockUp: "/MyWebsite/Arts/DharaMockUp.png",
  },
  {
    title: "Portrait",
    imageUrl: "/MyWebsite/Arts/Yeonjun.png",
    imageMockUp: "/MyWebsite/Arts/YeonjunMockUp.png",
  },
  {
    title: "Portrait",
    imageUrl: "/MyWebsite/Arts/Heva.png",
    imageMockUp: "/MyWebsite/Arts/HevaMockUp.png",
  },
  {
    title: "Logo",
    imageUrl: "/MyWebsite/Arts/EC4You.png",
    imageMockUp: "",
  },
  {
    title: "Logo",
    imageUrl: "/MyWebsite/Arts/FreshCoastalCleaners.png",
    imageMockUp: "",
  },
  {
    title: "Concept Art",
    imageUrl: "/MyWebsite/Arts/AngelOnEarth2.png",
    imageMockUp: "/MyWebsite/Arts/AngelOnEarth2MockUp.png",
  },
  {
    title: "Concept Art",
    imageUrl: "/MyWebsite/Arts/Sravan1.png",
    imageMockUp: "/MyWebsite/Arts/Sravan1MockUp.png",
  },
  {
    title: "Concept Art",
    imageUrl: "/MyWebsite/Arts/Harshith2.png",
    imageMockUp: "/MyWebsite/Arts/Harshith2MockUp.png",
  },
  {
    title: "Concept Art",
    imageUrl: "/MyWebsite/Arts/Lalith1.png",
    imageMockUp: "/MyWebsite/Arts/Lalith1MockUp.png",
  },
  {
    title: "Photography",
    imageUrl: "/MyWebsite/Arts/PlantsNight.png",
    imageMockUp: "/MyWebsite/Arts/PlantsNightMockUp.png",
  },
  // Add more work items as needed
];

const DigitalArtPage: FunctionComponent = () => {
  const scrollElementRef = useRef<HTMLDivElement>(null);
  const cardContainerElementRef = useRef<HTMLDivElement>(null);
  const [focusedIndex, setFocusedIndex] = useState<number>(0);
  const navigate = useNavigate();

  const onButtonClick = useCallback(
    (pageId: any) => {
      navigate(pageId);
    },
    [navigate]
  );

  const scrollPage_daWidth =
    document.querySelector(".scrollpage-da")?.clientWidth;
  const paddingRight = scrollPage_daWidth! - 320;

  function handleScroll(): void {
    if (scrollElementRef.current) {
      const scrollLeft = scrollElementRef.current.scrollLeft;
      const newIndex = Math.floor(scrollLeft / 320);
      setFocusedIndex(newIndex);
    }
    // throw new Error("Function not implemented.");
  }

  return (
    <div className="dummy-bg">
      <motion.div
        initial={{ rotateZ: 90, opacity: 0, transformOrigin: "100% 100%" }}
        animate={{ rotateZ: 0, opacity: 1 }}
        exit={{ rotateZ: -90, opacity: 0, transformOrigin: "100% 100%" }}
        transition={{ duration: 2 }}
      >
        <div className="fullpage-da">
          <img
            className="master-display-da"
            src={works[focusedIndex]?.imageMockUp || ""}
          />
          <div
            className="scrollpage-da"
            onScroll={handleScroll}
            ref={scrollElementRef}
          >
            <div className="xblock-da" style={{ paddingRight: paddingRight }}>
              {works.map((work, index) => (
                <div className="card-da">
                  <div className="work-item-da">
                    <img src={work.imageUrl} />
                    <h3>{work.title}</h3>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <button id="works-button" onClick={() => onButtonClick("/programs")}>
            <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
              <path
                id="circlePath"
                d="M 5, 50 a 45,45 0 1,1 90,0 45,45 0 1,1 -90,0"
                fill="none"
              />
              <text>
                <textPath href="#circlePath" startOffset="6%">
                  Programming
                </textPath>
              </text>
            </svg>
          </button>
        </div>
      </motion.div>
      <button id="home-button" onClick={() => onButtonClick("/")}>
        <FontAwesomeIcon icon={faHome} className="home-button-icon" />
      </button>
    </div>
  );
};

export default DigitalArtPage;
